    color:#4EF73F !important;
}


.animationTarget{
    -webkit-animation: animation 750ms linear both!important;
    animation: animation 750ms linear both!important;
}.animationTarget1{
    -webkit-animation: animation 750ms linear 0.2s both!important;
    animation: animation 750ms linear 0.2s both!important;

}.animationTarget2{
    -webkit-animation: animation 750ms linear 0.4s both!important;
    animation: animation 750ms linear 0.4s both!important;

