    -webkit-animation: animation 750ms linear 0.6s both!important;
    animation: animation 750ms linear 0.6s both!important;

}.animationTarget4{
    -webkit-animation: animation 750ms linear 0.8s both!important;
    animation: animation 750ms linear 0.8s both!important;
     animation-delay: 0.88s;
       -webkit-animation-delay: 0.8s;
}.animationTarget5{
    -webkit-animation: animation 750ms linear 1s both!important;
    animation: animation 750ms linear 1s both!important;
     animation-delay: 1s;
       -webkit-animation-delay: 1s;
}.animationTarget6{
    -webkit-animation: animation 750ms linear 1.2s both!important;
    animation: animation 750ms linear 1.2s both!important;
     animation-delay: 1.2s;
       -webkit-animation-delay: 1.2s;
}







